.designation {
  font-size: 20px;
  margin-bottom: 0px;
  font-weight: bold;
}

.organization{
  font-style: italic;
  font-size: 17px;
  margin-bottom: 0px;
}

main {
  margin: 0 auto;
  max-width: 900px;
  width: 80%;
}

.date{
  color: #808080;
  font-size: 14px;
  font-style: italic;
  margin-bottom: 0px;
}

.project {
  font-size: 20px;
  margin-bottom: 0px;
  font-weight: bold;
}


.display-picture {

    border-right: 2px solid black;
    padding: 50px;
    border-radius: 2005px;
}


@media  only screen and  (max-width: 1000px) {
  main {
    margin: none;
    max-width: none;
    width: 100%;
  }

  td {
    display: block;
  }

  .display-picture {
    border-right: none;
    padding: none;
    border-radius: none;
  }
}